      <p class="bold">Who Will know the information we ask?</p>
      <p>In general, only the personnel of our entity that is duly authorized may have knowledge of the information that we request.</p>
      <p>Similarly, may be aware of your personal information those entities that need to have access to it so that we can provide our services. For example, our bank will know your data if the payment of our services is made by card or bank transfer.</p>
      <p>Likewise, your information will be known by public or private entities to which we are obliged to provide your personal data due to compliance with any law. To give you an example, the Tax Law requires to provide the Tax Agency with certain information about economic operations that exceed a certain amount.</p>
      <p>In the event that, regardless of the aforementioned assumptions, we need to disclose your personal information to other entities, we will request your permission in advance through clear options that will allow you to decide in this regard.</p>


      <p class="bold">How are we going to protect your data?</p>
      <p>We will protect your data with effective security measures based on the risks involved in the use of your information.</p>
      <p>For this, our entity has approved a Data Protection Policy and annual controls and audits are carried out to verify that your personal data is safe at all times.</p>

      <p class="bold">Will we send your data to other countries?</p>
      <p>In the world there are countries that are safe for their data and others that are not so secure. For example, the European Union is a secure environment for your data. Our policy is not to send your personal information to any country that is not safe the point of view of protecting your data.
        <p>In the event that, in order to provide the service, it is essential to send your data to a country that is not as safe as Spain, we will always request your permission and apply effective security measures that reduce the risks of sending your information. personal to another country.</p>

        <p class="bold">For how long are we going to keep your data?</p>
        <p>We will keep your data during our relationship and as long as the laws force us. Once the applicable legal deadlines have been finalized, we will proceed to eliminate them in a safe and environmentally friendly manner.</p>

        <p class="bold">What are your data protection rights?</p>
        <p>At any time you can contact us to know what information we have about you, rectify it if it is incorrect and eliminate it once our relationship is finished, in case this is legally possible.</p>
        <p>You also have the right to request the transfer of your information to another entity. This right is called "portability" and may be useful in certain situations.</p>
        <p>To request any of these rights, you must make a written request to our address, along with a photocopy of your ID, to be able to identify you.</p>
        <p>In the offices of our entity we have specific forms to request these rights and we offer our help to complete them.</p>
        <p>To learn more about your data protection rights, you can consult the website of the Spanish Agency for Data Protection (www.agpd.es).</p>

        <p class="bold">Can you withdraw your consent if you change your mind at a later time?</p>
