<template lang="html">


  <div class="area-tecnica--view area-tecnica">

    <intro></intro>

    <section class="sec-center desc-area pad-sec">
      <div class="container waypoints" data="fadeInUp">
        <h3 class="title title-36">{{$t('area_tecnica.asesoramos.intro')}}</h3>
        <p>{{$t('area_tecnica.asesoramos.texto_1')}}</p> 
        <p>{{$t('area_tecnica.asesoramos.texto_2')}}</p>
        <a class="btn btn-dark btn-center" href="mailto:customerservice@ajusa.es">{{$t('area_tecnica.asesoramos.boton')}}</a>
      </div>
    </section>
    
    <tips-tecnicos></tips-tecnicos>

    <videos :intro="true"></videos>

    <webinarios></webinarios>

    <section class="asesoramiento sec-foto blue pad-sec">
