          <p class="go">{{$t('area_tecnica.asesoramiento.boton')}}<i class="fa fa-long-arrow-right" aria-hidden="true"></i></p>
        </div>
      </div>
    </section>

  <otras-secciones :data="['catalogo','actualidad']"></otras-secciones>


  </div>

</template>

<script lang="js">
  
  import Intro from '@/components/view-parts/intros/area-tecnica'

  import Otras_Secciones from '@/components/parts/acceso-a-secciones';

  import tips_tecnicos from '@/components/view-parts/area-tecnica/informes-tecnicos'

  import webinarios from '@/components/view-parts/area-tecnica/webinarios'

  import Videos from '@/components/view-parts/actualidad-parts/videos';
  

  export default  {
    name: 'area-tecnica',
    props: [],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    components: {
      'intro' : Intro,
      'otras-secciones' : Otras_Secciones,
      'tips-tecnicos' : tips_tecnicos,
      'webinarios' : webinarios,
      'videos' : Videos,
    }
  }
</script>

<style scoped>
.area-tecnica {

}
</style>



