<template lang="html">

  <section class="text-legal pad-sec">
    <div class="container">

      <div class="block-legal">
        <h3 class="title title-36">ПОЛИТИКА ИСПОЛЬЗОВАНИЯ КУКИ-ФАЙЛОВ</h3>
        <p class="bold">Информация, предоставляемая в соответствии с требованиями о защите персональных данных</p>
        <p class="bold">- Что такое куки-файлы и для чего мы их используем?</p>
        <p>Куки-файл – это небольшой файл с данными, который сохраняется в вашем браузере при каждом посещении нашей веб-страницы.</p>
        <p>Куки-файлы необходимы для отслеживания вашей активности на нашей веб-странице, чтобы при вашем следующем посещении распознать вас и адаптировать содержимое веб-страницы на основе ваших особенностей поведения в сети, пользовательских настроек и предпочтений.</p>
        <p>Куки-файл безопасен. Он не содержит в себе вредоносный или нежелательный код (например, вирусы, трояны, сетевые черви и т.д.), который может повредить ваше электронное устройство (компьютер, смартфон, планшет и т. д.). Тем не менее, куки-файл в некоторой степени затрагивает ваше право на защиту данных, поскольку он собирает определенную информацию о вас (особенности поведения в сети, пользовательские настройки, предпочтения и т. д.).</p>

        <p class="bold">Какую информацию хранит куки-файл?</p>
        <p>Как правило, куки-файлы не собирают персональные данные особых категорий (конфиденциальные данные). Данные, хранящиеся в куки-файлах, носят технический характер и включают в себя сведения о ваших личных предпочтениях, параметры персонализации содержимого и пр.</p>

        <p class="bold">Какие типы куки-файлов существуют?</p>
        <p>В целом, существует пять типов куки-файлов:</p>
        <ul>
          <li> <p class="bold">Технические куки-файлы:</p> 
            <p>Это основный тип куки-файлов. Такие файлы обеспечивают пользователю навигацию по веб-странице, платформе или приложению и позволяют использовать различные доступные параметры или услуги, такие как контроль трафика и передачи данных, идентификация сессии, доступ к данным с ограниченным доступом, запоминание данных об оформлении заказа, обработка покупки по заказу, оформление заявки на регистрацию или участие в мероприятии, использование средств безопасности при навигации, сохранение содержимого для воспроизведения видео или звука, а также обмен материалами через социальные сети.</p>
          </li>

          <li> <p class="bold">Персонализированные куки-файлы:</p> 
            <p>Это куки-файлы, которые дают пользователю доступ к услугам общего характера, подобранным на основе определенных параметров электронного устройства пользователя, таких как используемый язык, браузер, выбранное местоположение и т. д.</p>
          </li>

          <li> <p class="bold">Аналитические куки-файлы:</p>
            <p>Такие куки-файлы позволяют ответственному за них лицу отслеживать и анализировать действия пользователей на веб-страницах. Данные, собранные с помощью этих куки-файлов, используются для оценки активности на веб-страницах, платформе или в приложении, а также для создания статистики посещений этих веб-страниц, приложений и платформ, с целью внесения улучшений.</p>
          </li>

          <li> <p class="bold">Рекламные куки-файлы:</p>
            <p>Эти куки-файлы позволяют максимально эффективно управлять рекламным пространством (если таковое имеется), размещенным ответственным лицом на веб-странице, платформе или в приложении, через которые оказываются соответствующие услуги. Управление рекламным пространством осуществляется на основании определенных критериев, таких как характер публикуемого материала или частота отображения рекламы.</p></li>

            <li> <p class="bold">•  Поведенческие рекламные куки-файлы:</p> 
              <p>Эти куки-файлы позволяют максимально эффективно управлять рекламным пространством (если таковое имеется), размещенным ответственным лицом на веб-странице, платформе или в приложении, через которые оказываются соответствующие услуги. Такие куки-файлы хранят информацию о действиях пользователей, получаемую посредством непрерывного наблюдения за особенностями поведения в сети, на основе которых разрабатывается особый профиль для отображения рекламы.</p>
            </li>

          </ul>

          <p class="bold">Что такое собственные и сторонние куки-файлы?</p>


          <ul>
            <li>
             <p class="bold">Собственные куки-файлы:</p>
             <p>Собственные куки-файлы создаются и управляются лицом, ответственным за предоставление услуги по запросу пользователей.</p>
           </li>
           <li><p class="bold">Сторонние куки-файлы:</p>
            <p>Такие куки-файлы создаются сторонними ресурсами (внешними сервисами, такими как Google).</p>
          </li>
        </ul>



        <p class="bold">Какие куки-файлы мы используем на нашей веб-странице?</p>
        <p>Далее приведен список куки-файлов, которые мы используем на нашей веб-странице, с указанием цели их использования:</p>

        <ul >
          <li class="no-disc">
            <p class="bold">- Тип куки-файла</p>
            <p>
              Сторонние куки-файлы, аналитические куки-файлы: Google Analytics 
            </p>
          </li>
          <li class="no-disc">
            <p class="bold">- Цель</p>
            <p>Отслеживание и анализ поведения пользователя на веб-странице. Данные, собранные с помощью этих куки-файлов, используются для оценки активности на веб-страницах и создания статистики посещений этих веб-страниц, приложений и платформ, с целью внесения улучшений.
            </p>
          </li>
          <li class="no-disc">
            <p class="bold">
              - Когда используются куки-файлы и где они хранятся?
            </p>
            <p>
              Куки-файлы собирают информацию об активности пользователей во время просмотра веб-страницы. Данные хранятся в приложении Google Analytics.
            </p>
          </li>

        </ul>
        <p>Куки-файлы собирают информацию об активности пользователей во время просмотра веб-страницы. Данные хранятся в приложении Google Analytics.</p>
        <p class="bold">Что я могу сделать с куки-файлами?</p>
        <p>Вы можете разрешить использование куки-файлов, удалить или заблокировать их путем соответствующей настройки вашего браузера.</p>
        <p>Вы можете отказаться от установки куки-файлов на свой компьютер с помощью настроек своего браузера в любой момент времени, однако в таком случае мы не можем гарантировать правильную работу различных функций нашей веб-страницы.</p>
        <p>Ниже представлены ссылки на инструкции, которые помогут вам управлять куки-файлами и блокировать их в зависимости от вашего браузера:</p>
        <p >Internet Explorer: <u><a href="http://windows.microsoft.com/es-xl/internet-explorer/delete-manage-cookies#ie=ie-10">http://windows.microsoft.com/es-xl/internet-explorer/delete-manage-cookies#ie=ie-10</a></u></p>
        <p > FireFox: <u><a href="http://support.mozilla.org/es/kb/habilitar-y-deshabilitar-cookies-que-los-sitios-we">http://support.mozilla.org/es/kb/habilitar-y-deshabilitar-cookies-que-los-sitios-we</a></u></p>
        <p >Chrome: <u><a href="https://support.google.com/chrome/answer/95647?hl=es">https://support.google.com/chrome/answer/95647?hl=es</a></u></p>
        <p >Safari: <u><a href="https://www.apple.com/legal/privacy/es/cookies/">https://www.apple.com/legal/privacy/es/cookies/</a></u></p>
        Вы также можете удалить куки-файлы, сохраненные в вашем браузере, перейдя в соответствующий раздел в настройках браузера. 


      </div>

    </div>
  </section>

