import messages from '../translations';

let idiomas = new Object();

for(let lang in messages){
	idiomas[lang] = messages[lang].enlaces;
}

const shared = ['/','/404','/blog','/ajusa'];
const langAllowed = ['es','br','ru','en'];

let langSelected = "en";
let dominio; 

(location.hostname != "localhost") ? dominio = location.hostname  : dominio = "ajusa.com.br";
let path = location.pathname;

let idioma = navigator.language.substring(0, 2);
(idioma.includes('pt')) ? idioma = 'br' : '';
(!langAllowed.includes(idioma)) ? idioma = "en" : '';

if(location.search == "?change"){
	window.sessionStorage.setItem('canView', true);	
}

export function langdetector() {

	if(!!!window.sessionStorage.getItem('canView')){

		if(path == "/"){
			domainRedirection(dominio,idioma);
		}

		var pathShared;

		shared.forEach( e => {
			(path.includes(e)) ? pathShared = true:'';
			domainRedirection(dominio,idioma,path);
		});

		if(pathShared){
			domainRedirection(dominio,idioma,path);
		}else{


			path = '/'+path.split("/")[1];
			let URLdata = giveMeInfoOfThisPath(path);
			console.log(URLdata);
			let to = idiomas[idioma][URLdata.key];


			if(URLdata.lang != idioma){
				location.href = to;
			}

			domainRedirection(dominio,idioma,to);


		}


		return langSelected;
		

	}else{



		return dominio.split(".").pop();
	}

}


function domainRedirection(dominio, idioma, path){

	(!!!path) ? path = "" : '';

	
	switch (idioma) {

		case 'ru':
		(dominio.indexOf(".ru") == -1) ? location.replace("https://ajusa.ru"+path) : langSelected = "ru";
		break;

		case 'br':
		(dominio.indexOf("com.br") == -1) ? location.replace("https://ajusa.com.br"+path) : langSelected = "br";
		break;

		case 'es':
		(dominio.indexOf(".es") == -1) ? location.replace("https://ajusa.es"+path) : langSelected = "es";
		break;

		case 'en':
		langSelected = "en";
		break;

	}

}


function giveMeInfoOfThisPath(path){

	let dataURL = {
		lang : '',
		key : ''
	};

	for(let lang in idiomas){

		let array = Object.entries(idiomas[lang]);

		array.forEach( function(url) {		

			if(url[1] == path){
				dataURL.lang = lang,
				dataURL.key = url[0]
			}

		});

	}

	return dataURL;
}




