<template lang="html">

  <section class="text-legal pad-sec">
    <div class="container">

      <div class="block-legal">
        <h3 class="title title-36">POLITICA DE COOKIES</h3>
        <p class="bold">Informações em conformidade com os regulamentos de proteção de dados pessoais</p>
        <p class="bold">- O que são os cookies e para que servem?</p>
        <p>Um cookie é um pequeño arquivo de dados que é armazenado em seu navegador de internet cada vez que você visitar nosso site.</p>
        <p>A utilizade dos cookies são para guardar um histórico da sua atividade em nossa página web, para que quando a visite novamente se possa identificar e configurar automáticamente o conteúdo com base em seus hábitos de navegação, identificação e preferências.</p>
        <p>Um cookie é inofensivo, não contém qualquer malware ou código malicioso (por exemplo, vírus, trojans, worms, etc) que pode danificar o seu equipamento (computador, Smartphone, Tablet, etc), mas tem algum impacto sobre o seu direito à proteção de dados, uma vez que recolhe certas informações sobre você (hábitos de navegação, identidade, preferências, etc.).</p>

        <p class="bold">Quais informações são armazenadas por um cookie?</p>
        <p>Normalmente, os cookies não recolhem categorias especiais de dados pessoais (dados confidenciais). Os dados que armazenam são de natureza técnica, preferências pessoais, personalização de conteúdos, etc.</p>

        <p class="bold">Quais tipos de cookies existem?</p>
        <p>De um modo geral,esistem cinco tipos de programas cookies:</p>
        <ul>
          <li> <p class="bold">Cookies Técnicos:</p> 
            <p>Estes são os cookies mais básicos. Eles acompanham a nevegação do usuário por uma página da Web, plataforma ou aplicativo e aramazena as diferentes opções ou serviços disponíveis, como, por exemplo, tráfego e a comunicação de dados, identificações de sessão, acesso a áreas restritas, lembrando os itens que compõem uma escolha, processos ou solicitação de uma ordem de compra, solicitação ou registro de participação em um evento, uso de elementos de segurança durante a navegação, armazenando os conteúdos para a disseminação de vídeos, som ou compartilhamento através de mídias sociais durente a sua navegação.</p>
          </li>

          <li> <p class="bold">Cookies de Personalização:</p> 
            <p>Estes são os cookies que permitem ao usuário acessar o serviço com determinadas características gerais predefinidas de acordo com uma série de critérios no terminal do usuário, como o idioma, o tipo de navegador usado para acessar o serviço, a configuração regional de que o serviço é acessado, etc.</p>
          </li>

          <li> <p class="bold">Cookies Analiticos:</p>
            <p>Estes são os cookies que permitem que a parte responsável por eles monitorem e analisem o comportamento dos usuários nos sites que estão ligados. As informações recolhidas através destes cookies são utilizadas para medir a atividade dos websites, aplicação ou plataforma, para a preparação de perfis de navegação para os utilizadores destes websites, aplicações e plataformas, possam intriduzir melhorias.</p>
          </li>

          <li> <p class="bold">Cookies de Publicidade:</p>
            <p>Estes cookies permitem uma gestão mais eficiente dos espaços publicitários, se houver, que o responsável pelo programa incluiu em uma página Web, aplicação ou plataforma a partir do qual o desenvolvedor fornece o serviço ou opção de compra solicitado ou recém pesquizado com base em critérios como o conteúdo publicados ou a frequência com que são apresentados anúncios nas pesquisas do usuario.</p></li>

            <li> <p class="bold">Cookie de publicidade comportamental:</p> 
              <p>Estes cookies permitem uma gestão mais eficiente dos espaços publicitários, se houver, que o responsável pelo programa incluiu em uma página Web, aplicação ou plataforma a partir da qual o serviço solicitado é prestado. Esses cookies armazenam informações sobre o comportamento dos usuários, obtidas por meio da observação contínua de seus hábitos de navegação, permitindo assim que desenvolvam um perfil específico para exibir a propaganda com base neste perfil.</p>
            </li>

          </ul>

          <p class="bold">O que são cookies próprios ou de terceiros?</p>


          <ul>
            <li>
             <p class="bold">Cookies próprios:</p>
             <p>São cookies criados e controlados pelo responsável pela prestação do serviço solicitado pelo usuario.</p>
           </li>
           <li><p class="bold">Cookies de terceros:</p>
            <p>São aqueles cookies gerados por entidades não responsáveis pelo site visitado ( Serviços externos ou buscadores como Google )</p>
          </li>
        </ul>



        <p class="bold">Que tipo de cookies o nosso site armazena?</p>
        <p>A seguir listamos os tipos de cookies armazenados em nosso website e sua finalidade:</p>

        <ul >
          <li class="no-disc">
            <p class="bold">- Tipo de Cookie</p>
            <p>
              Cookies de terceros, analíticos Google Analytics
            </p>
          </li>
          <li class="no-disc">
            <p class="bold">- Propósito</p>
            <p>Monitoramento e análise do comportamento do usuário no site. As informações coletadas através deste tipo de cookie é usado para medir a atividade em sites, criar perfis de navegação para os usuários desses sites, aplicações e plataformas, a fim de fazer melhorias com base na análise dos dados sobre como os visitantes usam o Serviço
            </p>
          </li>
          <li class="no-disc">
            <p class="bold">
              - Quando e como é feito o armazenamento
            </p>
            <p>
              O cookie registra a atividade do usuário em tempo real durante a navegação e os dados são armazenados no aplicativo do Google Analytics.
            </p>
          </li>

        </ul>
        <p>O site da AJUSA também funciona com o aplicativo de armazenamento local.</p>
        <p class="bold">O que posso fazer com os cookies?</p>
        <p>Os cookies podem ser apagados, aceitos ou bloqueados como você desejar. Para isso, você só precisa configurar o seu navegador da Web da maneira que lhe atenda.</p>
        <p>A qualquer momento, você pode impedir a instalação de cookies em seu computador usando a opção correspondente de seu navegador, porém neste caso não podemos garantir o funcionamento correto das várias características em nosso site.</p>
        <p>Abaixo, fornecemos links para gerenciar e bloquear cookies, dependendo do navegador que você usa:</p>
        <p >Internet Explorer: <u><a href="http://windows.microsoft.com/es-xl/internet-explorer/delete-manage-cookies#ie=ie-10">http://windows.microsoft.com/es-xl/internet-explorer/delete-manage-cookies#ie=ie-10</a></u></p>
        <p > FireFox: <u><a href="http://support.mozilla.org/es/kb/habilitar-y-deshabilitar-cookies-que-los-sitios-we">http://support.mozilla.org/es/kb/habilitar-y-deshabilitar-cookies-que-los-sitios-we</a></u></p>
        <p >Chrome: <u><a href="https://support.google.com/chrome/answer/95647?hl=es">https://support.google.com/chrome/answer/95647?hl=es</a></u></p>
        <p >Safari: <u><a href="https://www.apple.com/legal/privacy/es/cookies/">https://www.apple.com/legal/privacy/es/cookies/</a></u></p>
       Você também pode excluir os cookies que você armazenou no seu navegador entrand nas opções de configuração.


      </div>

    </div>
  </section>

</template>

