<template lang="html">

 
  <section class="vh intro">

    <a href="#" v-scroll-to="'.excelencia'" class="scrolldown"></a>


      <div class="col-50 flex column nowrap justify-center align-center animated fadeInLeft">
        <div class="container-text">
          <h2 class="title title-52" v-html="$t('home.intro.titulo')"></span> </h2>
          <p>{{$t('home.intro.texto')}}</p>
          <a class="btn btn-light open-pop" @click="videoshow = true">{{$t('home.intro.boton')}}</a>
          <video-visualizer :id="$t('home.intro.video')" :show="videoshow" @closeVideo="videoshow=false"></video-visualizer>
        </div>
      </div>
    </div>

    <video loop autoplay :muted="muted" class="video-bkg" id="video">
      <source src="/static/video/video.mp4" type="video/mp4">
      </video>
      <button class="toggle-sound" @click="toggleSound" :muted="muted"></button>
    </section>

  </template>

  <script lang="js">
    import videoVisualizer from '@/components/parts/video-visualizer'
    export default  {
      name: 'home',
      props: [],
      mounted() {


      },
       components : {
      'video-visualizer' : videoVisualizer
    },
      data() {
        return {
          muted : true,
          videoshow : false,
          abierto :false,
        }
      },
      methods: {  
        toggleSound : function(){
          this.muted = !this.muted;
        }
      },
      computed: { 

      }
    }
  </script>

  <style scoped>
  .toggle-sound{
    position: absolute;
    right: 40px;
    bottom: 20px;
    width: 30px;
    height: 30px;
    display: inline-block;
    border: none;
    z-index: 5;
    outline: none;
    cursor: pointer;
    background: url(/static/img/sound-on.svg) center/30px no-repeat;
  }

  .toggle-sound[muted="muted"]{
    background: url(/static/img/sound-off.svg) center/30px no-repeat;
  }
</style>



