<template lang="html">

 <div class="language row-center ais">

   <div class="flags">

    <a :href="'https://ajusa.com.br'+urlsCambioIdioma.br+'?change'" v-if="hostname != 'br'">
      <img class="bandera" :src="`/static/img/change-to-br.png`" alt="cambiar idioma">
      <span>PT</span>
    </a>
    <a :href="'https://ajusa.ru'+urlsCambioIdioma.ru+'?change'" v-if="hostname != 'ru'">
      <img class="bandera" :src="`/static/img/change-to-ru.png`" alt="cambiar idioma">
      <span>RU</span>
    </a>
    <a :href="'https://ajusa.es'+urlsCambioIdioma.es+'?change'" v-if="hostname != 'es'">
      <img class="bandera" :src="`/static/img/change-to-es.png`" alt="cambiar idioma">
      <span>ES</span>
    </a>

    <a @click="requestLangChange">
      <img v-if="this.getRealLanguage != 'en'" class="bandera" :src="`/static/img/change-to-en.png`" alt="cambiar idioma">
      <span v-if="this.getRealLanguage == 'en'">{{hostname}}</span>
      <img v-if="this.getRealLanguage == 'en'" class="bandera" :src="`/static/img/change-to-${hostname}.png`" alt="cambiar idioma">
      <span v-if="this.getRealLanguage != 'en'">EN</span>
    </a>

  </div>

</div>

</template>

<script lang="js">

  import {mapGetters,mapActions} from 'vuex';

  import texts from '@/translations'

  import router from '@/router'

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);

  export default  {
    name: 'idioma',
    props: ['show'],
    data(){
      return {
        oldLang : ''
      }
    },
    methods: {
      toggle : function(){
        this.$emit('toggle');
      },
      requestLangChange : function(){

        this.oldLang = this.getLanguage;

        let changeTo = (this.getRealLanguage != 'en') ? 'en' : this.hostname;

        this.changeLanguage(changeTo)
        .then(r => {
          this.$i18n.locale = r;
          this.changePath(r); 
        }).catch(e =>{
          console.log(e);
        })
      },
      changePath : function (newLanguage){

        let enlaces = texts[this.oldLang].enlaces;
        let path;
        let newPath;

        let forbidden = ["/noticia/", "/news/"];

        for(let e in enlaces){
          (enlaces[e] == this.$route.path) ? path = e : '';
        }

        if(!!path){
          newPath = texts[this.getLanguage].enlaces[path];
          router.push(newPath); 
        }else{

          if(forbidden.some(e => this.$route.path.includes(e))){

            axios.get(`post/${this.$route.params.id}`)
            .then(response => {
              let slugs = response.data.noticia.slugs;
              newPath = "/blog/";
              if(newLanguage == "es"){
                (!!slugs.es) ? newPath += `noticia/${slugs.es}` : '';
              }else{
                (!!slugs.en) ? newPath += `news/${slugs.en}` : '';
              }
              router.push(newPath); 
            })
            .catch(error => console.log(error)); 

          }

        }

        this.toggle();

      },
      ...mapActions(['changeLanguage']),
    },
    computed: {
     ...mapGetters({
      getServer : 'getServer',
      getLanguage : 'getLanguage',
      getRealLanguage : 'getRealLanguage',
      getTexts : 'getTexts',
      getAllTexts : 'getAllTexts',
    }),
     urlsCambioIdioma : function(){

      let path = this.$route.path;

      let enlaces = this.getAllTexts[this.getRealLanguage].enlaces;

      let destination;

      let urls = {es:'',en:'',ru:'',br:''};


      for(let e in enlaces){
        if(enlaces[e] == path){
          destination = e;
        }
      }
      
      if(!!destination){
        urls.es=this.getAllTexts.es.enlaces[destination];
        urls.en=this.getAllTexts.en.enlaces[destination];
        urls.ru=this.getAllTexts.ru.enlaces[destination];
        urls.br=this.getAllTexts.br.enlaces[destination];
      }

      return urls;

    },
    hostname : function(){
      var hostname = location.hostname.split('.').pop();
      return hostname;
    }
  }
}
</script>

<style scoped>


.language a{
  display: block;
  margin: 0 6px;
  position: relative;
  max-width: 40px;
}

.language a span{
  position: absolute;
  left: 0;
  bottom: -20px;
  height: 20px;
  width: 100%;
  display: inline-block;
  text-align: center;
  opacity: 0;
  transition: .5s;
  text-transform: uppercase;
}

.language a:hover span{
  opacity: 1;
}

.language a:hover img{
  border: 1px solid white;
  transition: .5s;
}

.bandera{
  max-width: 60px;
  width: 100%;
  display: block;
  border: 1px solid #000;
}

.language .cerrar{
  font-size: 40px;
}

.flags{
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  -webkit-justify-content: center;
  -ms-align-items: center;
  align-items: center;
}

@media screen and (max-width: 1024px){
  .language{
    text-align: center;
  }
}
@media only screen and (max-width: 500px) {
  .bandera{
    width: 30px;
  }

  .language-cover{
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    justify-content: center;
    -webkit-justify-content: center;
  }

  .flags{
    margin: 20px 0;
  }
}

</style>



