<template lang="html">
  <section class="destacados pad-sec" v-if="noticiasProcessed.length > 0">
    <div class="container">
      <h2 class="title title-36">{{$t("blog.noticias_relacionadas")}}</h2>
      <div class="flex wrap justify-between">
        <template v-if="!!noticias">
          <noticia-el v-for="(e, key) in noticiasProcessed" :data="e" :key="key"></noticia-el>
        </template>
      </div>
    </div>
    <router-link class="btn btn-dark btn-center" v-if="!small" :to="$t('enlaces.blog')" target="_blank">{{$t('actualidad.noticias.ir_blog')}}</router-link>
  </section>

</template>

<script lang="js">


  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios' 

  Vue.use(VueAxios, axios);

  import Noticia_el from '@/components/parts/noticia-el';

  import { mapGetters } from 'vuex'


  export default  {
    name: 'noticias-relacionadas',
    props: ['small','cat', 'titulo'],
    mounted() {

      axios.get(`blog/${this.cat}?lang=${this.lang}`)
      .then(response => {
        this.noticias = response.data.noticias;
      })
      .catch(error => console.log(error));
    },
    data() {
      return {
        noticias : ''
      }
    },
    computed: {
      noticiasProcessed : function (){

        let noticias;

        if(this.noticias){
          let count = (this.small) ? '3' : this.noticias.length;
          let actual = this.$route.params.id;
          let noticiasToFilter = this.noticias.slice(0,count);
          noticias = noticiasToFilter.filter(n => n.titulo != this.titulo);
        }

        return noticias;

      },
      ...mapGetters({
        lang: 'getLanguage',
      }),

    },
    components: {
      'noticia-el' : Noticia_el,
    }
  }
</script>

<style scoped>
.noticias {

}
</style>



