<template lang="html">

  <section class="vh intro">
        <div class="bkg-pieza waypoints-instant -responsiveIntro" data="fadeInUp">
          <a href="#" v-scroll-to="'.cat-1'" class="scrolldown"></a>
          <div class="col-50 flex column nowrap justify-center align-center animated fadeInLeft -introText">
            <div class="container-text">
              <h2 class="title title-52">{{$t('productos.intro.titulo')}}</h2>
              <p>{{$t('productos.intro.texto')}}</p>
            </div>
          </div>
        </div>
        <video autoplay muted loop class="video-bkg">
        <source src="/static/video/video-productos-intro.mp4" type="video/mp4">
        </video>
      </section>

</template>

<script lang="js">
  export default  {
    name: 'productos',
    props: [],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {

    }
}
</script>

<style scoped>
  .video-bkg {
    width: 100%;
    display: block; 
  }

  .productos .intro .bkg-pieza{
    z-index: 5;
  }
@media only screen and (max-width: 500px) {
  .intro .-responsiveIntro{
     position: relative;
     background-image:url(/static/img/bkg/bkg-menu.jpg);
     background-repeat: no-repeat;
     background-size: cover;

  }
  .intro video{
    display: none;
  }
  .-introText{
    position: relative;
  }
  .productos .intro .bkg-pieza{
    position: relative;
  }
  .intro .container-text{
    margin-top: 80px;
  }
  

}
@media only screen and (max-width: 900px) {
  .intro .-responsiveIntro{
     position: relative;
     background-image:url(/static/img/bkg/bkg-menu.jpg);
     background-repeat: no-repeat;
     background-size: cover;

  }

  .intro video{
    display: none;
  }
  .-introText{
    position: relative;
  }
  .productos .intro .bkg-pieza{
    position: relative;
  }
  .intro .container-text{
       margin-top: 150px;
    margin-bottom: 30px;
  }
   .-responsiveIntro .container-text .title.title-52{
   font-size: 26px; 
  }
}


</style>



