<template lang="html">

  <section class="vh intro flex wrap justify-center">
    <a href="#" v-scroll-to="'.cat-general'" class="scrolldown"></a>
    <div class="col-50 flex column justify-center animated fadeInLeft">
      <div class="container-text">

        <h2 class="title title-52">{{$t('catalogo.intro.titulo')}}</h2>

        <p>{{$t('catalogo.intro.texto')}}</p>

        <ul class="menu-catalogo flex row justify-start">
          <li><a v-scroll-to="'.cat-general'">{{$t('enlaces.textos.catalogo_general')}}</a></li>
          <li><a v-scroll-to="'.cat-turbo'">{{$t('enlaces.textos.catalogo_turbos')}}</a></li>
          <li> <a :href="'http://ows-cdn.tecdoc.net/ajusa/index.jsp?language='+ getLanguage" target="_blank">{{$t('enlaces.textos.tec_alliance')}}</a></li>
        </ul> 
      </div>
    </div>

    <div class="col-50 flex column justify-center animated fadeInLeft">
      <div class="container-text -catalogoImpreso">
        <h2 class="title title-36" v-html="$t('catalogo.intro.catalogo_impreso')"></h2>
        <p>{{$t('catalogo.intro.catalogo_impreso_texto')}}</p>
        <router-link class="btn btn-light" :to="`${$t('enlaces.contacto')}/#catalogo`">{{$t('catalogo.intro.catalogo_impreso_boton')}}</router-link>
      </div>
    </div>
  </section>

</template>

<script lang="js">
import {mapGetters} from 'vuex';
  export default  {
    name: 'catalogo',
    props: [],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {
      ...mapGetters(['getServer', 'getLanguage']),
    }
  }
</script>

<style scoped>
.catalogo {

}
</style>



