// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
/* eslint-disable */
import Vue from 'vue'
import App from './App'
import router from './router'
import {langdetector} from '@/langdetector';

localStorage.removeItem('language');

//setIdiomaInicial 
localStorage.setItem('language', langdetector());

//VUEX 
import Vuex from 'vuex'
Vue.use(Vuex)


const store = new Vuex.Store({
  state :{
    language: localStorage.getItem('language'),
    server : 'https://back.ajusa.es/',
    textos : messages[localStorage.getItem('language')],
    allTextos : messages,
    likes : JSON.parse(localStorage.getItem('likes')) || new Array(),
    hints : JSON.parse(localStorage.getItem('hints')) || new Array()
  },
  actions :{
    changeLanguage : ({commit}, changeTo) => {
      return new Promise((resolve, reject) => {
        commit('setLanguage', changeTo);
        switch (changeTo) {
          case "es":
          Validator.localize('es', validatorEs);
          break;
          case "en":
          Validator.localize('en', validatorEn);
          break;
          case "br":
          Validator.localize('br', validatorEn);
          break;
          case "ru":
          Validator.localize('ru', validatorEn);
          break;
        }
        resolve(changeTo);
      });
    },
    sendLike : ({commit}, slug) => {
      return new Promise((resolve, reject) => {
        axios.get(`like/${slug}`).then(response => {
          resolve(response.data.likes);
          commit('addLike', slug)
        })
        .catch(function (error) {
          reject(false);
        });
      });    
    },
    sendHint : ({commit}, slug) => {
      return new Promise((resolve, reject) => {
        axios.get(`hint/${slug}`).then(response => {
          resolve(response.data.hints);
          commit('addHint', slug)
        })
        .catch(function (error) {
          reject(false);
        });
      });    
    }
  },
  getters : {
    getLanguage (state){
      let lang = (state.language != 'es') ? 'en' : 'es';
      return lang;
    },
    getRealLanguage : state => state.language,
    getServer : state => state.server,
    getTexts : state => state.textos,
    getAllTexts : state => state.allTextos,
    getlike(state) {
      return (slug) => {
        return state.likes.some(l => l == slug)
      }
    },
    gethint(state) {
      return (slug) => {
        return state.hints.some(l => l == slug)
      }
    }
  },
  mutations : {
    setLanguage (state, lang){
      console.log(lang);
      state.language = lang;
    },
    addLike(state, slug){
      state.likes.push(slug);
      window.localStorage.setItem('likes',JSON.stringify(state.likes));
    },
    addHint(state, slug){
      state.hints.push(slug);
      window.localStorage.setItem('hints',JSON.stringify(state.hints));
    }
  }
});

//.VUEX 


//AXIOS
import axios from 'axios'
import VueAxios from 'vue-axios'
axios.defaults.baseURL = store.state.server;
//.AXIOS

//VEE-VALIDATE
import VeeValidate, {Validator} from 'vee-validate';
import validatorEs from '@/validator/es';
import validatorEn from '@/validator/en';
import validatorRu from '@/validator/ru';
import validatorPt from '@/validator/pt';

switch (window.localStorage.getItem('language')) {
  case 'es':
  Validator.localize('es', validatorEs);
  break;
  case 'en':
  Validator.localize('en', validatorEn)
  break;
  case 'ru':
  Validator.localize('ru', validatorRu)
  break;
  case 'pt':
  Validator.localize('pt', validatorPt)
  break;
}

Vue.use(VeeValidate);
//.VEE-VALIDATE


//IDIOMAS
import VueI18n from 'vue-i18n'
import messages from './translations';

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: store.state.language, 
  messages,
});
//.IDIOMAS


//SCROLLTO

var VueScrollTo = require('vue-scrollto'); 
Vue.use(VueScrollTo,{
  offset: -120,
})


Vue.config.productionTip = false

new Vue({
	el: '#app',
	router,
	i18n,
	store,
  VeeValidate,
  VueScrollTo,
  components: { App },
  template: '<App/>'
})



