<template lang="html">

  <div class="catalogo--view catalogo">

    <intro></intro>
    
    <section class="cat-general sec-center pad-sec">
      <div class="container waypoints" data="fadeInUp">
        <h6 class="title title-36">{{$t('enlaces.textos.catalogo_general')}}</h6>
        <p>{{$t('catalogo.cat_general.texto')}}</p>
      </div>
    </section>

    <buscador></buscador>

    <section class="cat-turbo pad-sec">
      <div class="container waypoints flex wrap" data="fadeInUp">
        <div class="col-100 flex column justify-center">
          <div class="container texto">
            <h3 class="title title-36">{{$t('catalogo.cat_turbo.titulo')}}</h3>
            <p>{{$t('catalogo.cat_turbo.texto')}}</p>
          </div>
        </div>
        <div class="col-100 flex row justify-around">
          <a class="go" :href="getServer+'bundles/prensa/descargables/public/pdfs/Turbos_por_vehiculo.zip'">{{$t('catalogo.cat_turbo.por_vehiculo')}} <i class="fa fa-long-arrow-right" aria-hidden="true"></i></a>
          <a class="go" :href="getServer+'bundles/prensa/descargables/public/pdfs/Turbos_por_datos_turbo.zip'">{{$t('catalogo.cat_turbo.por_turbo')}} <i class="fa fa-long-arrow-right" aria-hidden="true"></i></a>
        </div>
      </div>
    </section>

    <section class="tec-alliance sec-foto blue pad-sec">
      <div class="container waypoints flex row-reserve justify-start" data="fadeInLeft">
        <div class="col-50">
          <a :href="'http://ows-cdn.tecdoc.net/ajusa/index.jsp?language='+getLanguage" target="_blank"></a>
          <h4>{{$t('catalogo.tec_alliance.intro')}}</h4>
          <h3 class="title title-52">{{$t('catalogo.tec_alliance.titulo')}}</h3>
          <p>{{$t('catalogo.tec_alliance.texto_1')}}</p> 
          <p>{{$t('catalogo.tec_alliance.texto_2')}}</p>
          <p class="go">{{$t('catalogo.tec_alliance.boton')}}<i class="fa fa-long-arrow-right" aria-hidden="true"></i></p>
        </div>
      </div>
    </section>
    
    <banner-ajusa-th></banner-ajusa-th>


    <otras-secciones :data="['area_tecnica','solicite_catalogo']"></otras-secciones>



  </div>

</template>

<script lang="js">

  import Intro from '@/components/view-parts/intros/catalogo'
  import Buscador from '@/components/view-parts/catalogo-parts/buscador'

  import bannerAjusaTh from '@/components/parts/banner-ajusa-th'

  import Otras_Secciones from '@/components/parts/acceso-a-secciones';

  import {mapGetters} from 'vuex';


  export default  {
    name: 'catalogo',
    props: [],
    mounted() {

    },
    data() {
      return {
        showAjusath : false
      }
    },
    computed: {
      ...mapGetters(['getServer', 'getLanguage']),
    },
    components: {
      'intro' : Intro,
      'buscador'  : Buscador,
      'otras-secciones' : Otras_Secciones,
      'banner-ajusa-th' : bannerAjusaTh

    }
  }
</script>

<style scoped>
.catalogo {

}
</style>



