        <div class="block-legal">
          <h3 class="title title-36">POLÍTICA DE PROTEÇÃO DE DADOS</h3>
          <p>A direção/órgão diretivo da AUTO JUNTAS SAU (doravante, a pessoa responsável pelo tratamento), assume a máxima responsabilidade e compromisso com o estabelecimento, implementação e manutenção desta política de protecção de dados, garantindo a melhoria contínua do controlador de dados. O objetivo de alcançar a excelência no que respeita ao cumprimento do Regulamento (UE) 2016/679 do Parlamento Europeu e do Conselho, de 27 de abril de 2016, relativo à proteção das pessoas fisicas no que diz respeito ao tratamento de dados pessoais e à livre circulação destes dados e que revoga a Diretiva 95/46/CE (regulamento geral de proteção de dados) (OJEU L 119/1, 04-05-2016) e a legislação espanhola sobre a proteção de dados pessoais (lei orgânica, legislação setorial específica e seus regulamentos de desenvolvimento).</p>
          <br>
          <p>A política de proteção de dados da AUTO JUNTAS SAU conta com o princípio da responsabilidade pró-ativa, segundo o qual o responsável pelo controle é o cumprimento do quadro normativo e jurisprudencial que rege a referida política e é capaz de autoridades de controlo competentes.</p>
          <br>
          <p>Neste sentido, a pessoa responsável pelo tratamento será regida pelos seguintes princípios que devem servir também a todo o seu pessoal como guia e quadro de referência no tratamento de dados pessoais:</p>
          <p>1. Proteção dos dados do projeto: o controlador será aplicado, tanto no momento da determinação dos meios de tratamento e no momento do tratamento propriamente dito, medidas técnicas e organizacionais apropriadas, como a pseudoutilização, projetada para aplicar efetivamente o princípios de proteção de dados, tais como a minimização de dados, e integrar as garantias necessárias no tratamento.</p>
          <p>2. Proteção de dados por padrão: o controlador de dados aplicará as medidas técnicas e organizacionais apropriadas para garantir que, por padrão, somente os dados pessoais necessários para cada um dos propósitos específicos do processamento serão processados.</p>
          <p>3. Proteção de dados no ciclo de vida da informação: as medidas que garantem a proteção dos dados pessoais serão aplicáveis durante todo o ciclo de vida das informações.</p>
          <p>4. Legalidade, lealdade e transparência: os dados pessoais serão tratados de forma legal, justa e transparente em relação à parte interessada.</p>
          <p>5. Limitação por objetivo: os dados pessoais serão coletados para fins específicos, explícitos e legítimos, e não serão processados de forma incompatível com os ditos propósitos.</p>
          <p>6. Minimização dos dados: os dados pessoais serão adequados, pertinentes e limitados ao que é necessário em relação aos fins para os quais são tratados.</p>
          <p>7. Exatidão: os dados pessoais serão exatos e somente quando necessário. Devem ser tomadas todas as medidas razoáveis para que os dados pessoais imprecisos relativamente aos fins para os quais são tratados sejam corrigidos sem demora.</p>
          <p>8. Limitação do período de conservação: os dados pessoais serão mantidos de forma a permitir a identificação das partes interessadas durante não mais tempo do que o necessário para efeitos de tratamento de dados pessoais.</p>
          <p>9. Integridade e confidencialidade: os dados pessoais serão tratados de forma a garantir a segurança adequada dos dados pessoais, incluindo a proteção contra o tratamento não autorizado ou ilegal e contra a perda, destruição ou danos acidentais, através da aplicação de medidas técnicas ou organizacionais de segurança adequadas.</p>
          <p>10. Informação e formação: uma das chaves para garantir a proteção dos dados pessoais é a formação e informação prestada ao pessoal envolvido no seu tratamento. Durante o ciclo de vida da informação, todo o pessoal com acesso aos dados serão devidamente treinados e informado sobre suas obrigações em relação ao cumprimento das regulamentações de proteção de dados.</p>
          <br>
          <p>A política de proteção de dados da AUTO JUNTAS SAU é comunicada a todo o pessoal  responsável pelo tratamento de dados e disponibilizada a todas as partes interessadas.</p>
          <br>
          <p>Consequentemente, esta política de proteção de dados envolve todo o pessoal responsável pelo tratamento de dados, que deve conhecê-la e assumi-la, considerando-a como sua, assim como cada membro responsável pela sua aplicação, atentando as regras de proteção de dados aplicáveis a sua atividade, bem como identificar e contribuir com as oportunidades de melhoria que julgar adequadas com o objetivo de alcançar a excelência em relação a segurança de dados.</p>
          <br>
          <p>Esta política será revista pela direção/órgão diretivo da AUTO JUNTAS SAU, quantas vezes a mesma julgar necessária, para adaptar, em todos os momentos, às atuais disposições relativas à proteção de dados pessoais.</p>
        </div>
        
        
      </div>
