    title : 'Каталог',
    description : 'Каталог компании AJUSA является наиболее полным каталогом в автомобильной отрасли и в секторе запасных частей. Компания AJUSA предлагает два каталога - общий каталог с абсолютно всеми наименованиями компонентов для двигателей, производимых ею, а также каталог турбо-компонентов.'
