historia : {
  texto_intro : "Мы являемся всемирным эталоном вот уже на протяжении 45 лет. Мы находимся в постоянном поиске совершенства как нашей продукции, так и наших услуг.",
  hoy : "Наши дни",
  texto_1972_1 : "Компания AJUSA была основана в 1972 г. В том же году была выпущена первая прокладка головки блока цилиндров. Ее производство заняло 3 часа и было осуществлено полностью вручную!",
  texto_1972_2 : "Мы посетили первые международные выставки и увеличили площадь наших производственных объектов, переехав в промышленную зону Кампольяно.",
  texto_1980_1 : "Мы выпустили наш первый общий каталог с 600 наименованиями. Текущий каталог содержит в 20 раз больше артикулов! Мы превысили отметку в 100 000 наименований, обеспечив полное покрытие запчастями азиатские и европейские марки.",
  texto_1980_2 : "Нам удалось изготовить нашу первую деталь ОЕМ-качества - прокладку головки блока цилиндров для компании Renault.",
  texto_1990_1 : "Мы открыли научно-исследовательский и испытательный центр, в котором проводим наиболее исчерпывающие проверки качества нашей продукции.",
  texto_1990_2 : "Запуск 3 новых производственных линий: болтов головки блока цилиндров, гидрокомпенсаторов и распределительных валов.",
  texto_2000_1 : "Компания Ajusa растет на международном уровне и открывает филиалы в России, Великобритании, Мексике и Бразилии, включая стратегических партнеров по всему миру.",
  texto_2000_2 : "Мы переезжаем в бизнес-парк, который носит наше имя - Ajusa. Речь идет о 40 000 кв.м., отведенных под наш завод, и 3 000 кв.м., занимаемых нашими офисными помещениями.",
  texto_2010_1 : "С 2014 г. мы производим трубки для подачи масла, а также выпустили наш каталог турбокомпрессоров с самым большим числом наименований на рынке.",
  texto_2010_2 : "Мы применяем новейшие технологии для обеспечения максимального качества нашей продукции.",
},
