<template lang="html">

  <div class="historia">

    <section class="vh intro">
      <a v-scroll-to="'.timeline-hist'" class="scrolldown"></a>
      <div class="col-50 flex column nowrap justify-center align-center animated fadeInLeft">
        <div class="container-text">
          <h2 class="title title-52">{{$t('enlaces.textos.historia')}}</h2>
          <p>{{$t('historia.texto_intro')}}</p>
        </div>
      </div>
    </section>



    <div class="historia--wrapper">


      <div class="timeline__wrapper">
        <div class="timeline">
          <ul class="timeline__list">

            <li class="timeline__item timeline__item--active" @click="moveTo(0)">
              <a class="timeline__link"><div class="timeline__item__point"></div></a>
              <div class="timeline__item__year">1972 1980</div>
            </li>

            <li class="timeline__item" @click="moveTo(1)">
              <a class="timeline__link"><div class="timeline__item__point"></div></a>
              <div class="timeline__item__year">1980 1990</div>
            </li>

            <li class="timeline__item" @click="moveTo(2)">
              <a class="timeline__link"><div class="timeline__item__point"></div></a>
              <div class="timeline__item__year">1990 2000</div>
            </li>

            <li class="timeline__item" @click="moveTo(3)">
              <a class="timeline__link"><div class="timeline__item__point"></div></a>
              <div class="timeline__item__year">2000 2010</div>
            </li>

            <li class="timeline__item" @click="moveTo(4)">
              <a class="timeline__link"><div class="timeline__item__point"></div></a>
              <div class="timeline__item__year">2010 {{$t('historia.hoy')}}</div>
            </li>

          </ul>
        </div>
      </div>

      <section>

        <div class="fullpage-wp timeline-hist" v-fullpage="opts" ref="example">

          <article class="page page-0">
            <div class="view view--1 slick-active">
              <div class="view__inner">
                <div class="flex wrap justify-center time-wrap timeline01">
                  <div class="col-50 flex column justify-center">
                    <div class="mask mask-pic-year flex column justify-center">
                    </div>
                  </div>
                  <div class="col-50 mask flex column justify-center">
                   <div class="color flex column justify-center">
                    <div>
                      <p>{{$t('historia.texto_1972_1')}}</p>
                      <p>{{$t('historia.texto_1972_2')}}</p>
                    </div>
                    <div class="mask mask-img2 flex column justify-center">

                      <slick class="slider-container" :options="slickOptions">
                        <div class="slide" data-value="timeline01-img2"></div>
                        <div class="slide" data-value="timeline01-img7"></div>
                        <div class="slide" data-value="timeline01-img8"></div>
                        <div class="slide" data-value="timeline01-img9"></div>
                        <div class="slide" data-value="timeline01-img3"></div>
                        <div class="slide" data-value="timeline01-img4"></div>
                        <div class="slide" data-value="timeline01-img5"></div>
                        <div class="slide" data-value="timeline01-img6"></div>

                      </slick>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </article>


        <article class="page page-1">
          <div class="view view--2">
            <div class="view__inner">
              <div class="flex wrap row-reverse justify-center time-wrap timeline02">
                <div class="col-50 flex column justify-center">
                  <div class="mask mask-pic-year flex column justify-center">
                  </div>
                </div>
                <div class="col-50 mask flex column justify-center">
                 <div class="color flex column justify-center">
                  <div>
                    <p>{{$t('historia.texto_1980_1')}}</p>
                    <p>{{$t('historia.texto_1980_2')}}</p>
                  </div>
                  <div class="mask mask-img2 flex column justify-center">
                    <slick class="slider-container" :options="slickOptions">
                      <div class="slide" data-value="timeline02-img2"></div>
                      <div class="slide" data-value="timeline02-img3"></div>
                      <div class="slide" data-value="timeline02-img4"></div>
                      <div class="slide" data-value="timeline02-img5"></div>
                    </slick>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </article>



      <article class="page page-2">
        <div class="view view--3">
          <div class="view__inner">
            <div class="flex wrap justify-center time-wrap timeline03">
              <div class="col-50 flex column justify-center">
                <div class="mask mask-pic-year flex column justify-center">
                </div>
              </div>
              <div class="col-50 mask flex column justify-center">
               <div class="color flex column justify-center">
                <div>
                  <p>{{$t('historia.texto_1990_1')}}</p>
                  <p>{{$t('historia.texto_1990_2')}}</p>
                </div>
                <div class="mask mask-img2 flex column justify-center">
                 <slick class="slider-container" :options="slickOptions">
                  <div class="slide" data-value="timeline03-img2"></div>
                  <div class="slide" data-value="timeline03-img3"></div>
                  <div class="slide" data-value="timeline03-img4"></div>
                  <div class="slide" data-value="timeline03-img5"></div>
                </slick>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </article>


  <article class="page page-3">
    <div class="view view--4">
      <div class="view__inner">
        <div class="flex wrap row-reverse justify-center time-wrap timeline04">
          <div class="col-50 flex column justify-center">
            <div class="mask mask-pic-year flex column justify-center">
            </div>
          </div>
          <div class="col-50 mask flex column justify-center">
           <div class="color flex column justify-center">
            <div>
              <p>{{$t('historia.texto_2000_1')}}</p>
              <p>{{$t('historia.texto_2000_2')}}</p>
            </div>
            <div class="mask mask-img2 flex column justify-center">
             <slick class="slider-container" :options="slickOptions">
              <div class="slide" data-value="timeline04-img2"></div>
              <div class="slide" data-value="timeline04-img3"></div>
              <div class="slide" data-value="timeline04-img4"></div>
              <div class="slide" data-value="timeline04-img5"></div>
            </slick>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</article>



<article class="page page-4">

  <div class="view view--5">
    <div class="view__inner">
      <div class="flex wrap row-reverse justify-center time-wrap timeline05">
        <div class="col-50 flex column justify-center">
          <div class="mask mask-pic-year flex column justify-center">
          </div>
        </div>
        <div class="col-50 mask flex column justify-center">
         <div class="color flex column justify-center">
          <div>
            <p>{{$t('historia.texto_2010_1')}}</p>
            <p>{{$t('historia.texto_2010_2')}}</p>
          </div>
          <div class="mask mask-img2 flex column justify-center">
           <slick class="slider-container" :options="slickOptions">
            <div class="slide" data-value="timeline05-img2"></div>
            <div class="slide" data-value="timeline05-img3"></div>
            <div class="slide" data-value="timeline05-img4"></div>
            <div class="slide" data-value="timeline05-img5"></div>
            
          </slick>
        </div>
      </div>
    </div>
  </div>
</div>
</div>

</article>

</div>

</section>


</div>





</div>

</template>

<script lang="js">

  import 'animate.css'
  import 'fullpage-vue/src/fullpage.css'
  import VueFullpage from 'fullpage-vue'

  import Vue from 'vue';

  Vue.use(VueFullpage)

  import Slick from 'vue-slick';

  export default  {
    name: 'historia',
    props: [],
    mounted() {
      window.document.body.onscroll = function() {
        event.preventDefault();
      }
    },
    data() {
      return {
       slickOptions: {
        slidesToShow: 1,
        arrows : true
      },
      opts: {
        start: 0,
        dir: 'h',
        duration: 500,
        movingFlag : true,
        der: 0.5,
        beforeChange: function (prev, next) {

        },
        afterChange: function (prev, next) {
          $(".page .view").removeClass('slick-active');
          $(`.page-${next} .view`).addClass('slick-active');
          $(`.timeline__item`).removeClass('timeline__item--active');
          $(`.timeline__item`).eq(next).addClass('timeline__item--active');
        }
      }
    }
  },
  methods: {
   moveNext(){
    this.$refs.example.$fullpage.moveNext();
  },
  movePrev(){
    this.$refs.example.$fullpage.movePrev();
  },
  moveTo(value){
    this.$refs.example.$fullpage.moveTo(value, true);
    $(`.menu-desplazamiento button`).removeClass('active');
    $(`.menu-desplazamiento button[data-id="${value}"]`).addClass('active');
  },
  scroll(){
    console.log("scrooll");
  }
},
components: {
  'slick':Slick,
}
}
</script>

<style scoped>
.historia--wrapper{
  width: 100%;
  max-height: 100vh;
  position: relative;
  pointer-events: none;
}

.mask-img2,
.timeline__wrapper{
  pointer-events: visible;
}

.fullpage-wp{
  pointer-events: none;
}

.page{
  min-height: 100vh;
}
@media (max-height: 1358px) and (min-width: 767px) {
  .timeline-hist .mask .color{
    padding-top: 120px;
  }
}
@media (max-height: 1358px) and (max-width: 768px) {
  .timeline-hist .mask .color{
    padding-top: 20px;
  }
}

</style>



