<template lang="html">

  <div class="ajusa--view ajusa">

    <intro></intro>

    <section>
      <ul class="menu-nosotros flex row">
        <li class="flex column justify-center" v-scroll-to="'#aj-calidad'"><a href="#">{{$t('ajusa.enlaces.calidad')}}</a></li>
        <li class="flex column justify-center" v-scroll-to="'#aj-servicios'"><a href="#">{{$t('ajusa.enlaces.servicio')}}</a></li>
        <li class="flex column justify-center" v-scroll-to="'#aj-compromiso'"><a href="#">{{$t('ajusa.enlaces.compromiso')}}</a></li>
        <li class="flex column justify-center" v-scroll-to="'#aj-equipo'"><a href="#">{{$t('ajusa.enlaces.equipo')}}</a></li>
        <li class="flex column justify-center" v-scroll-to="'#aj-catalogo'"><a href="#">{{$t('ajusa.enlaces.catalogo')}}</a></li>
      </ul>
    </section>


    <section id="aj-calidad" class="sec-ajusa">
      <div class="flex wrap">
        <article class="img-aj col-75  waypoints" data="fadeInUp">
        </article>
        <article class="container-text col-25 waypoints" data="fadeInRight">
          <h3 class="title title-72">{{$t('ajusa.calidad.titulo')}}</h3>
          <p>{{$t('ajusa.calidad.texto')}}</p>
        </article>
      </div>
    </section>

    <section id="aj-servicios" class="sec-ajusa">
      <div class="flex row-reverse wrap">
        <article class="img-aj col-75  waypoints" data="fadeInUp">
        </article>
        <article class="container-text col-25 waypoints" data="fadeInLeft">
          <h3 class="title title-72">{{$t('ajusa.servicio.titulo')}}</h3>
          <p>{{$t('ajusa.servicio.texto')}}</p>
        </article>
      </div>
    </section>

    <section id="aj-compromiso" class="sec-ajusa">
      <div class="flex wrap">
        <article class="img-aj col-60 waypoints" data="fadeInUp">
        </article>
        <article class="container-text col-40 waypoints" data="fadeInRight">
          <h3 class="title title-72" v-html="$t('ajusa.compromiso.titulo')"></h3>
          <p>{{$t('ajusa.compromiso.texto')}}</p>
        </article>
      </div>
    </section>

    <section id="aj-equipo" class="sec-ajusa">
      <div class="flex row-reverse wrap">
        <article class="img-aj col-60 waypoints" data="fadeInUp">
        </article>
        <article class="container-text col-40 waypoints" data="fadeInLeft">
          <h3 class="title title-72">{{$t('ajusa.equipo.titulo')}}</h3>
          <p>{{$t('ajusa.equipo.texto')}}</p>
        </article>
      </div>
    </section>

    <section id="aj-catalogo" class="sec-ajusa" :lang="getRealLanguage">
      <div class="flex wrap">
        <article class="img-aj col-75 waypoints" data="fadeInUp">
        </article>
        <article class="col-25">
          <div class="flex wrap pad-sec text-ajusa waypoints" data="fadeInUp">
            <article class="col-100 flex row justify-end waypoints" data="fadeInLeft">
              <h3 class="title title-72">{{$t('ajusa.catalogo.titulo')}}</h3>
            </article>
            <article class="container-text col-100 flex row justify-start waypoints" data="fadeInRight">
              <p>{{$t('ajusa.catalogo.texto')}}</p>
            </article>
          </div>    
        </article>
      </div>
      
    </section>

    <section class="sec-center pad-sec">
      <div class="container waypoints" data="fadeInUp">
        <h6 class="title title-36">{{$t('ajusa.excelencia.titulo')}}</h6>
        <p>{{$t('ajusa.excelencia.texto')}}<strong class="bold"><a target="_blank" v-html="$t('ajusa.excelencia.enlace_texto')"></a></strong><span  v-html="$t('ajusa.excelencia.texto_2')"></span></p>
         <a class="btn btn-dark" target="_blank" :href="getServer+ $t('enlaces.productos_certificados')">{{$t('ajusa.excelencia.boton')}}</a>
      </div>
    </section>


    <section class="block-historia sec-foto blue pad-sec">
      <div class="container waypoints flex row-reserve justify-start" data="fadeInLeft">
        <div class="col-50">
          <router-link :to="$t('enlaces.historia')"></router-link>
          <h3 class="title title-52">{{$t('ajusa.historia.titulo')}}</h3>
          <p>{{$t('ajusa.historia.texto')}}</p>
          <p class="go">{{$t('ajusa.historia.boton')}}<i class="fa fa-long-arrow-right" aria-hidden="true"></i></p>
        </div>
      </div>
    </section>


    <banner-ajusa-th></banner-ajusa-th>
    
    <otras-secciones :data="['actualidad','area_tecnica']"></otras-secciones>


  </div>

</template>

<script lang="js">

  import Intro from '@/components/view-parts/intros/ajusa'
  
  import Otras_Secciones from '@/components/parts/acceso-a-secciones';

    import bannerAjusaTh from '@/components/parts/banner-ajusa-th';
     import {mapGetters} from 'vuex';


  export default  {
    name: 'ajusa',
    props: [],
    mounted() {
      $(window).scroll(function(event) {

        var distancia = $(window).scrollTop();
        var altura = $(window).height();

        if(distancia >= 575){
          $(".menu-nosotros").addClass('fixed');
          $(".menu-catalogo-h").addClass('show');
          $(".solicitar-cat").addClass('show');
        }else{
          $(".menu-nosotros").removeClass('fixed');
          $(".menu-catalogo-h").removeClass('show');
          $(".solicitar-cat").removeClass('show');
        }
      });
    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {
      ...mapGetters(['getServer', 'getLanguage', 'getRealLanguage']),
    },
    components: {
      'otras-secciones' : Otras_Secciones,
      'intro' : Intro,
      'banner-ajusa-th' : bannerAjusaTh

    }
  }
</script>

<style scoped>
.btn-dark {
  margin-top: 20px;
}
</style>



