<template lang="html">

  <header class="menu-open">

    <div class="h-top animated slideInDown">

      <router-link class="logo-ajusa" to="/"></router-link>

      <ul class="menu-catalogo-h flex row justify-start" v-if="inCatalogo">
        <li><a v-scroll-to="'.cat-general'">{{$t('enlaces.textos.catalogo_general')}}</a></li>
        <li><a v-scroll-to="'.cat-turbo'">{{$t('enlaces.textos.catalogo_turbos')}}</a></li>
        <li> <a :href="'http://ows-cdn.tecdoc.net/ajusa/index.jsp?language='+ getLanguage" target="_blank">{{$t('enlaces.textos.tec_alliance')}}</a></li>
      </ul>


      <button :class="['icon-menu', show_menu ? 'active' : '']" @click="toggleMenu">
        <span></span>
        <span></span>
        <span></span>
      </button>
      <router-link v-if="inCatalogo" class="solicitar-cat" :to="`${$t('enlaces.contacto')}/#catalogo`" v-scroll-to="'#solicita-catalogo'" :data-text="$t('enlaces.textos.catalogo_impreso')"><span>{{$t('enlaces.textos.catalogo_impreso')}}</span></router-link > 

    </div>
    
    <nav :class="['overlay-menu', show_menu ? 'open' : '']">
      <div class="col-50 flex column justify-center">
        <ul class="menu">
          <li><router-link to="/">{{$t('enlaces.textos.inicio')}}</router-link></li>
          <li><router-link :to="$t('enlaces.productos')">{{$t('enlaces.textos.productos')}}</router-link></li>
          <li><router-link :to="$t('enlaces.catalogo')">{{$t('enlaces.textos.catalogo')}}</router-link></li>
          <li><router-link :to="$t('enlaces.area_tecnica')">{{$t('enlaces.textos.area_tecnica')}}</router-link></li>
          <li><router-link to="/ajusa">{{$t('enlaces.textos.ajusa')}}</router-link></li>
          <li><router-link :to="$t('enlaces.actualidad')">{{$t('enlaces.textos.actualidad')}}</router-link></li>
          <li><router-link :to="$t('enlaces.contacto')">{{$t('enlaces.textos.contacto')}}</router-link></li>
        </ul>
        <ul class="redes flex row justify-start">
          <li><a class="fa fa-twitter" href="https://twitter.com/ajusa_spain?lang=es" target="_blank"></a></li>
          <li><a class="fa fa-facebook" href="https://www.facebook.com/AjusaSpain/" target="_blank"></a></li>
          <li><a class="fa fa-instagram" href="https://www.instagram.com/ajusaspain/" target="_blank"></a></li>
          <li><a class="fa fa-youtube" href="https://www.youtube.com/channel/UCnoYVeFbsPiOVOcf80tW6Bg" target="_blank"></a></li>
        </ul>
      </div>
    </nav>
  </header>

</template>

<script lang="js">
import {mapGetters} from 'vuex';
  export default  {
    name: 'app-header',
    props: [],
    mounted() {

      this.inCatalogo = (this.$route.name == "catalogo") ? true : false;

      var altura = $(window).height();

      $(window).scroll(function(event) {

        var distancia = $(window).scrollTop();

        if(distancia >= 260){
          $(".h-top").addClass('min');
          $(".menu-catalogo-h").attr('style', 'opacity:1');
        }else{
          $(".menu-catalogo-h").attr('style', 'opacity:0');
          if($(".overlay-menu").hasClass('open') == false){
            $(".h-top").removeClass('min');
          }
        }

      });
    },
    data() {
      return {
        show_menu : false,
        inCatalogo : false
      }
    },
    methods: {
      toggleMenu : function (){
        this.show_menu = !this.show_menu;
      }
    },
     computed: {
      ...mapGetters(['getServer', 'getLanguage']),
    },
    watch: {
      '$route' : function() {
        this.show_menu = false;
        this.inCatalogo = (this.$route.name == "catalogo") ? true : false;
      }
    }
  }
</script>

<style scoped>
.header {

}
</style>



