<template lang="html">
  <div>
    <a :error="error" class="show-popup" @click="popup = true">{{$t('formulario.popup.claim')}}</a>
    <section class="popup-condiciones" v-show="popup">
      <div class="popup-container">
        <div class="pop-up">
          <h2 class="title title-26">{{$t('formulario.popup.intro')}}</h2>
          <template v-for="(el, key) in $t('formulario.popup.textos')">
          <h3 class="title title-18">{{el.titulo}}</h3>
          <p>{{el.texto}}</p>
          </template>
          <a class="btn btn-dark" @click="launchOk">{{$t('formulario.popup.boton')}}</a>
        </div>
      </div>
    </section>
  </div>

</template>

<script lang="js">
  export default  {
    name: 'popup-condiciones',
    props: ['error'],
    mounted() {

    },
    data() {
      return {
        popup: false
      }
    },
    methods: {
      launchOk : function(){
        this.popup = false;
        this.$emit('launch');
      }
    },
    computed: {

    }
  }
</script>

<style scoped >
.popup-condiciones {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.5);
  z-index: 101;
}

.popup-container{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  -webkit-justify-content: center;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}

.pop-up{
  width: 100%;
  max-width: 680px;
  padding: 30px;
  background: white;
  display: inline-block;
  position: relative;
  text-align: center;
}

.title-26{
  margin-bottom: 10px;
  font-size: 22px;
}

.title{
  text-align: left;
}

p{
  font-size: 14px;
  text-align: left;
}

.show-popup{
  font-size: 12px;
  color: #054279;
  text-decoration: underline;
  margin-top: 10px;
  display: inline-block;
  width: 100%;
}

.show-popup[error="true"]{
  color: red;
}

.btn{
  max-width: 260px;
  margin:0 auto;
  display: inline-block;
}

@media screen and (max-width: 500px){
  
  .pop-up{
    padding: 15px;
    overflow: scroll;
    max-height: 80vh;
  }

  .title-26{
    font-size: 18px;
    line-height: 20px;
  }

  .title-18{
    font-size: 14px;
  }

  .btn{
    padding: 10px 20px;
    font-size: 12px;
  }

  p{
    font-size: 10px;
    line-height: 12px;
  }
}

@media screen and (max-height: 840px){

  .pop-up{
    max-height: 90vh;
    overflow: scroll;
  }

}

</style>



