<template lang="html">

  <div class="download-pdf">
    <a class="pdf column-center" :href="server+data.archivo" target="_blank">
      <img :src="`/static/img/novedades-${lang}.jpg`" alt=""/>
      <p>{{data.tip_titulo}}</p>
    </a>
  </div>

</template>

<script lang="js">

  import { mapGetters } from 'vuex'


  export default  {
    name: 'tip-novedad',
    props: ['data'],
    mounted() {

    },
    data() {
      return {

      }
    },
    methods: {

    },
    computed: {
     ...mapGetters({
      server : 'getServer',
      lang : 'getLanguage'
    }),
   }
 }
</script>

<style scoped>

img{
  max-width: 100%;
}

.download-pdf{
  padding: 0;
}

.download-pdf .pdf{
  padding-bottom: 0px;
  background: none;
}

.download-pdf .pdf p{
  padding: 10px 10px 40px 10px;
  margin-top: 0;
  height: calc(100% - 186px);
  background: #E6ECF1 url(/static/img/arrow-down.svg) center calc(100% - 10px) no-repeat;

}

@media screen and (max-width: 768px){
  .download-pdf{
    width: 100%;
    margin-bottom: 20px;
    padding: 0;
  }

  .download-pdf:last-of-type{
    width: 100%;
    margin-bottom: 0;
    padding: 0;
  }
}

</style>



