<template lang="html">

  <div class="categoria-blog blog">


    <intro :data="$t(`blog.categorias_nombres.${this.$route.params.id}`)"></intro>

    <section class="destacados posts-cat">
      <div class="container">
        <p class="breadcrumb"><router-link :to="$t('enlaces.blog')">{{$t('enlaces.textos.blog')}}</router-link> / {{$t(`blog.categorias_nombres.${this.$route.params.id}`)}}</p>
        <div class="flex wrap justify-around pad-sec">
         <template v-if="!!noticias">
          <noticia-el v-for="(e, key) in noticias" :data="e" :key="key"></noticia-el>
        </template>
        <template v-if="noticias.length == 0 && !no_post">
          <h2 class="title title-76">{{$t('blog.no_hay_post')}}</h2>
        </template>
      </div>
    </div>
  </section>


  <ultimas-noticias :small="true"></ultimas-noticias>


</div>



</template>

<script lang="js">

  import Intro from '@/components/view-parts/intros/blog'

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  import { mapGetters } from 'vuex'

  Vue.use(VueAxios, axios);

  import Noticia_el from '@/components/parts/noticia-el';
  import Ultimas_noticias from '@/components/view-parts/actualidad-parts/noticias'
  
  export default  {
    name: 'categoria-blog',
    props: [],
    mounted() {
     axios.get(`blog/${this.$route.params.id}?lang=${this.lang}`)
     .then(response => {
      this.no_post = true;
      this.noticias = response.data.noticias;
    })
     .catch(error => console.log(error));
   },
   data() {
    return {
      noticias : '',
      no_post : false
    }
  },
  methods: {

  },
  computed : {
    ...mapGetters({
      lang: 'getLanguage',
    }),
  },
  components: {
    'intro' : Intro,
    'noticia-el' : Noticia_el,
    'ultimas-noticias' : Ultimas_noticias
  }
}
</script>

<style scoped>
.categoria-blog {

}
</style>



