<template lang="html">

  <div class="productos--view productos">

   <intro></intro>

   <section class="product-cat" :data="$t(`productos.shared['junta-culata']`)">
    <div class="cat-1 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat1">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.junta_culata.titulo')}}</h2>
        <p>{{$t('productos.junta_culata.texto')}}</p>
      </div>
      <div class="ver ver-responsive flex column justify-center">
        <a class="open-pop1 mas-light" @click="show('junta-culata')"></a>
        <rrss-share :data="$t(`productos.shared['junta-culata']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['junta-motor']`)">
    <div class="cat-2 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat2">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.junta_motor.titulo')}}</h2>
        <p>{{$t('productos.junta_motor.texto')}}</p>
      </div>
      <div class="ver ver-responsive column justify-center">
        <a class="open-pop2 mas-light" @click="show('junta-motor')"></a>
        <rrss-share :data="$t(`productos.shared['junta-motor']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['tornillos']`)">
    <div class="cat-3 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat3">
      <div class="desc-cat flex column justify-center ">
        <h2 class="title title-36">{{$t('productos.tornillos.titulo')}}</h2>
        <p>{{$t('productos.tornillos.texto')}}</p>
      </div>
      <div class="ver ver-responsive flex column justify-center">
        <a class="open-pop3 mas-light" @click="show('tornillos')"></a>
        <rrss-share :data="$t(`productos.shared['tornillos']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['retenes']`)">
    <div class="cat-4 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat4">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.retenes.titulo')}}</h2>
        <p>{{$t('productos.retenes.texto')}}</p>
      </div>
      <div class="ver ver-responsive column justify-center">
        <a class="open-pop4 mas-light" @click="show('retenes')"></a>
        <rrss-share :data="$t(`productos.shared['retenes']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['tubos-engrase']`)">
    <div class="cat-5 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat5">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.tubos_engrase.titulo')}}</h2>
        <p>{{$t('productos.tubos_engrase.texto')}}</p>
      </div>
      <div class="ver ver-responsive flex column justify-center">
        <a class="open-pop5 mas-light" @click="show('tubos-engrase')"></a>
        <rrss-share :data="$t(`productos.shared['tubos-engrase']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['junta-turbo']`)">
    <div class="cat-6 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat6">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.junta_turbo.titulo')}}</h2>
        <p>{{$t('productos.junta_turbo.texto')}}</p>
      </div>
      <div class="ver ver-responsive column justify-center">
        <a class="open-pop6 mas-light" @click="show('junta-turbo')"></a>
        <rrss-share :data="$t(`productos.shared['junta-turbo']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['ajusil']`)">
    <div class="cat-7 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat7">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.ajusil.titulo')}}</h2>
        <p>{{$t('productos.ajusil.texto')}}</p>
      </div>
      <div class="ver ver-responsive flex column justify-center">
        <a class="open-pop7 mas-light" @click="show('ajusil')"></a>
        <rrss-share :data="$t(`productos.shared['ajusil']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['taques']`)">
    <div class="cat-8 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat8">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.taques.titulo')}}</h2>
        <p>{{$t('productos.taques.texto')}}</p>
      </div>
      <div class="ver ver-responsive column justify-center">
        <a class="open-pop8 mas-light" @click="show('taques')"></a>
        <rrss-share :data="$t(`productos.shared['taques']`)"></rrss-share>
      </div>
    </div>
  </section>

  <section class="product-cat" :data="$t(`productos.shared['arbol-levas']`)">
    <div class="cat-9 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat9">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.arbol_levas.titulo')}}</h2>
        <p>{{$t('productos.arbol_levas.texto')}}</p>
      </div>
      <div class="ver ver-responsive flex column justify-center">
        <a class="open-pop9 mas-light" @click="show('arbol-levas')"></a>
        <rrss-share :data="$t(`productos.shared['arbol-levas']`)"></rrss-share>
      </div>
    </div>
  </section>
  
  <section class="product-cat" :data="$t(`productos.shared['otros']`)">
    <div class="cat-10 img-cat flex wrap justify-end align-stretch" data-value="bkg-cat10">
      <div class="desc-cat flex column justify-center">
        <h2 class="title title-36">{{$t('productos.otros.titulo')}}</h2>
        <p>{{$t('productos.otros.texto')}}</p>
      </div>
      <div class="ver ver-responsive column justify-center">
        <a class="open-pop10 mas-light" @click="show('otros')"></a>
        <rrss-share :data="$t(`productos.shared['otros']`)"></rrss-share>
      </div>
    </div>
  </section>



  <section class="popup" v-show="show_popup">
    <div class="wrapper-gallery">
      <div class="imagen--container flex column justify-center">
        <img :src="`/static/img/products/${img_big}.jpg`" alt="Placeholder" class="custom" @click="closePop">
      </div>
      <ul class="thumbnails">
        <li @click="setImage(`${how}-${key+1}`)" v-for="(i, key) in fotos[how]"><img :src="`/static/img/products/${how}-${key+1}.jpg`"></li>
      </ul>   
    </div>
  </section>

  <section class="garantia sec-center pad-sec">
    <div class="container waypoints" data="fadeInUp">
      <h6 class="title title-36" v-html="$t('productos.garantia.titulo')"></h6>
      <p>{{$t('productos.garantia.texto')}}</p>
      
      <a class="btn btn-dark" target="_blank" :href="getServer+ $t('enlaces.productos_certificados_calidad')">{{$t('productos.garantia.boton')}}</a>
    </div>
  </section>

  <otras-secciones :data="['catalogo','area_tecnica']"></otras-secciones>



</div>

</template>

<script lang="js">

  import Intro from '@/components/view-parts/intros/productos'

  import Otras_Secciones from '@/components/parts/acceso-a-secciones';
  import rrssShare from '@/components/parts/rrss-share';
  import {mapGetters} from 'vuex';
  

  export default  {
    name: 'productos',
    props: [],
    mounted() {
      let to = this.$route.fullPath.split("?")[1];
      if($(".product-cat[data='"+to+"']").length > 0){
        $('body,html').animate({ scrollTop: $(".product-cat[data='"+to+"']").position().top }, 800);
      }
    },
    data() {
      return {
        show_popup : false,
        fotos : {
          'junta-turbo' : 4,
          'ajusil' : 4,
          'arbol-levas' : 4,
          'junta-culata' : 3,
          'junta-motor' : 4,
          'otros' : 4,
          'retenes' : 4,
          'taques' : 4,
          'tornillos' : 4,
          'tubos-engrase' : 4
        },
        how : '',
        img_big : ''
      }
    },
    methods: {
      show : function(value){
        this.how = value;
        this.show_popup =  true;
        this.img_big = `${value}-1`;
      },
      setImage : function(value){
        this.img_big = value;
      },
      closePop : function(){
        this.show_popup = false;
      }
    },
    computed: {
      ...mapGetters(['getServer', 'getLanguage']),
    },
    components: {
      'intro' : Intro,
      'otras-secciones' : Otras_Secciones,
      'rrss-share' : rrssShare
    }
  }
</script>

<style scoped>
.popup{
  display: inline-block;
}

.imagen--container{
  max-width: 100%;
  max-height: 100%;
}

.imagen--container img{
  max-width: 100%;
  max-height: 100%;
}

.btn-dark{
  margin-top: 10px;
}
@media only screen and (max-width: 650px) {
  .ver.ver-responsive,
  .product-cat:nth-of-type(2n):hover .ver.ver-responsive{
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    display: flex;
  }

}

</style>



