<template lang="html">

  <section class="footer--container">

    <newsletter></newsletter>

    <a href="#" v-scroll-to="'.menu'" class="subir"></a>

    <footer>

      <div class="container flex wrap justify-between align-center">

       <a class="show-redes" @click="toggleRedes">{{$t('footer.siguenos')}}</a>

       <nav class="f-menu">

        <ul class="flex wrap justify-center">

          <li><router-link :to="$t('enlaces.sala_prensa')">{{$t('enlaces.textos.sala_prensa')}}</router-link></li>
          <li><router-link to="/blog" target="_blank">{{$t('enlaces.textos.blog')}}<i class="fa fa-external-link" aria-hidden="true"></i></router-link></li>
          <li><a href="http://www.ajusath.es/" target="_blank">{{$t('enlaces.textos.tecnologias_hidrogeno')}}<i class="fa fa-external-link" aria-hidden="true"></i></a></li>
          <li><router-link :to="$t('enlaces.contacto')">{{$t('enlaces.textos.contacto')}}</router-link></li>
          <li><router-link target="_blank" :to="$t('enlaces.mapa_web')">{{$t('enlaces.textos.mapa_web')}}</router-link></li>
          <li><router-link target="_blank" :to="$t('enlaces.politica')">{{$t('enlaces.textos.politica_privacidad')}}</router-link></li>
          <li><router-link target="_blank" :to="$t('enlaces.datos_seguros')">{{$t('enlaces.textos.datos_seguros')}}</router-link></li>
          <li><router-link target="_blank" :to="$t('enlaces.politica_cookies')">{{$t('enlaces.textos.politica_cookies')}}</router-link></li>
          <li><router-link target="_blank" :to="$t('enlaces.aviso_legal')">{{$t('enlaces.textos.aviso_legal')}}</router-link></li>

        </ul>

      </nav>


      <div class="contenedor-cambio">

        <idioma @toggle="toggleIdioma" :show="show_idioma"></idioma>

      </div>



    </div>


    <rrss-slide @toggle="toggleRedes" :show="show_redes"></rrss-slide>

  </footer>


</section>

</template>

<script lang="js">

  import Newsletter from '@/components/parts/newsletter';

  import RRSS_slide from '@/components/view-parts/footer-parts/rrss-slide'
  import Idioma from '@/components/view-parts/footer-parts/idioma'

  import {mapGetters} from 'vuex';

  export default  {
    name: 'app-footer',
    props: [],
    mounted() {

      $(window).scroll(function(event) {

        var distancia = $(window).scrollTop();

        if(distancia <= 1000){
          $(".subir").removeClass('fadeInUp');

          if($(".subir").hasClass('animated')){
            $(".subir").addClass('animated fadeOutDown');
          }
          
        }else{
          $(".subir").removeClass('animated fadeOutDown');
          $(".subir").addClass('animated fadeInUp');

        }


      });
    },
    data() {
      return {
        show_redes : false,
        show_idioma : false,
      }
    },
    computed : {
      ...mapGetters(['getRealLanguage'])
    },
    methods: {
      toggleRedes : function (){
        this.show_redes = !this.show_redes;
      },
      toggleIdioma : function (){
        this.show_idioma = !this.show_idioma;
      }
    },
    components :{
      'newsletter' : Newsletter,
      'rrss-slide' : RRSS_slide,
      'idioma' : Idioma
    }
  }
</script>

<style scoped>

footer{
  overflow: hidden;
}

.container {
  max-width: 1400px;
}
.f-menu {
  width: 50%;
}


.bandera{
  max-width: 40px;
  width: 100%;
  display: inline-block;
}

.contenedor-cambio{
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}

.contenedor-cambio span{
  margin-right: 10px;
  height: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

@media screen and (max-width: 1000px){
  .container{
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
  }
}

@media screen and (max-width: 500px){

  .container{
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
  }

}

</style>



