<template lang="html">

 <section class="videos pad-sec">

  <div class="texto container flex wrap justify-start" v-if="intro">
    <div class="col-50">
     <h3 class="title title-36">{{$t('actualidad.videos.titulo')}}</h3>
     <p>{{$t('actualidad.videos.texto')}}</p>
   </div>
 </div>

 <div class="container">
  <div class="block-layout">
    <h3 class="title title-26">{{$t('actualidad.videos.ultimos')}}</h3>
    <div class="flex wrap justify-between">
     <video-el v-for="(e, key) in videos" :data="e" :key="key" @show="sendToShow"></video-el>
   </div>
 </div>
 <a class="btn btn-dark btn-center" href="https://www.youtube.com/channel/UCnoYVeFbsPiOVOcf80tW6Bg" target="_blank">{{$t('actualidad.videos.ver_todos')}}</a>
</div>

<video-visualizer :show="show_video" :id="video_to_show" @closeVideo="closeVideo"></video-visualizer>

</section>
</template>

<script lang="js">

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);

  import Video_el from '@/components/parts/video-el';

  import VideoVisualizer from '@/components/parts/video-visualizer.vue';


  export default  {
    name: 'videos',
    props: ['intro'],
    created() {

      let url = 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyA_l78FldVWxqLuosRXYry3fgISeR65xPo&channelId=UCnoYVeFbsPiOVOcf80tW6Bg&part=snippet,id&order=date&maxResults=3'

      axios.get(url)
      .then(result => {
        this.videos = result.data.items;
      }).catch(e => {
        console.log(e);
      });

    },
    data() {
      return {
        videos : '',
        video_to_show : '',
        show_video : false
      }
    },
    methods: { 
      sendToShow : function(value){
        this.video_to_show = value;
        this.show_video = true;
      },
      closeVideo : function (){
        this.show_video = false;
        this.video_to_show = '';
      }
    },
    components: {
      'video-el' : Video_el,
      'video-visualizer': VideoVisualizer
    }
  }
</script>

<style scoped>
.videos {

}
</style>



