import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/views/home'
import Productos from '@/components/views/productos'
import Catalogo from '@/components/views/catalogo'
import Area_tecnica from '@/components/views/area-tecnica'
import Ajusa from '@/components/views/ajusa'
import Historia from '@/components/views/historia'

import Actualidad from '@/components/views/actualidad'
import Blog from '@/components/views/blog'
import Categoria_blog from '@/components/views/categoria-blog'
import Ficha_blog from '@/components/views/ficha-blog'
import SalaPrensa from '@/components/views/sala-prensa'

import Contacto from '@/components/views/contacto'
import TrabajaConNosotros from '@/components/views/trabaja-con-nosotros'

import PoliticaPrivacidad from '@/components/views/politica-privacidad'
import PoliticaPrivacidadEN from '@/components/views/politica-privacidad-en'
import PoliticaPrivacidadRU from '@/components/views/politica-privacidad-ru'
import PoliticaPrivacidadBR from '@/components/views/politica-privacidad-br'
import PoliticaCookies from '@/components/views/politica-cookies'
import PoliticaCookiesEN from '@/components/views/politica-cookies-en'
import PoliticaCookiesRU from '@/components/views/politica-cookies-ru'
import PoliticaCookiesBR from '@/components/views/politica-cookies-br'
import DatosSeguros from '@/components/views/datos-seguros'
import DatosSegurosEN from '@/components/views/datos-seguros-en'
import DatosSegurosRU from '@/components/views/datos-seguros-ru'
import DatosSegurosBR from '@/components/views/datos-seguros-br'
import AvisoLegal from '@/components/views/aviso-legal'
import AvisoLegalEN from '@/components/views/aviso-legal-en'
import AvisoLegalRU from '@/components/views/aviso-legal-ru'
import AvisoLegalBR from '@/components/views/aviso-legal-br'


import MapaWeb from '@/components/views/mapa-web'
import Error404 from '@/components/views/error-404'


Vue.use(Router);

import messages from '@/translations';

let lang = localStorage.getItem('language');
(!!!lang) ? lang = "es" : '';

let paths_en = messages.en.enlaces;
let paths_es = messages.es.enlaces;
let paths_ru = messages.ru.enlaces;
let paths_br = messages.br.enlaces;


const router = new Router({
	mode : 'history',
	routes: [
	{
		path: '/',
		name: 'home',
		component: Home,
		meta :{
			es  : {
				title : 'Ajusa',
				description: 'Ajusa ofrece una amplía gama de componentes para el motor y de piezas de recambio para vehículos. Fabricante de juntas de culata, tornillos, juego de juntas, sellante de silicona, retenes, tubos de engrase, juego de juntas para turbo, taqués hidráulicos y árboles de levas.'
			},
			en  : {
				title : 'Ajusa',
				description: 'AJUSA offers a wide range of engine components and spare parts for vehicles. AJUSA is a manufacturer of head gaskets, head bolts, gasket sets, silicone sealant, seals, oil feed pipes, turbo seal kits, hydraulic lifters and camshafts.'
			},
			ru  : {
				title : 'Ajusa',
				description: 'AJUSA offers a wide range of engine components and spare parts for vehicles. AJUSA is a manufacturer of head gaskets, head bolts, gasket sets, silicone sealant, seals, oil feed pipes, turbo seal kits, hydraulic lifters and camshafts.'
			},
			br  : {
				title : 'Ajusa',
				description: 'AJUSA offers a wide range of engine components and spare parts for vehicles. AJUSA is a manufacturer of head gaskets, head bolts, gasket sets, silicone sealant, seals, oil feed pipes, turbo seal kits, hydraulic lifters and camshafts.'
			}
		}
	},

	//PRODUCTOS
	{path: paths_es.productos,component: Productos,meta : paths_es.seo.productos},
	{path: paths_en.productos,component: Productos,meta : paths_en.seo.productos},
	{path: paths_ru.productos,component: Productos,meta : paths_ru.seo.productos},
	{path: paths_br.productos,component: Productos,meta : paths_br.seo.productos},

	//CATALOGO
	{path: paths_es.catalogo,component: Catalogo,meta : paths_es.seo.catalogo},
	{path: paths_en.catalogo,component: Catalogo,meta : paths_en.seo.catalogo},
	{path: paths_ru.catalogo,component: Catalogo,meta : paths_ru.seo.catalogo},
	{path: paths_br.catalogo,component: Catalogo,meta : paths_br.seo.catalogo},

	//AREA TECNICA
	{path: paths_es.area_tecnica,component: Area_tecnica,meta : paths_es.seo.area_tecnica},
	{path: paths_en.area_tecnica,component: Area_tecnica,meta : paths_en.seo.area_tecnica},
	{path: paths_ru.area_tecnica,component: Area_tecnica,meta : paths_ru.seo.area_tecnica},
	{path: paths_br.area_tecnica,component: Area_tecnica,meta : paths_br.seo.area_tecnica},

	//AJUSA
	{path: paths_es.ajusa,component: Ajusa,meta : paths_es.seo.ajusa},
	{path: paths_en.ajusa,component: Ajusa,meta : paths_en.seo.ajusa},
	{path: paths_ru.ajusa,component: Ajusa,meta : paths_ru.seo.ajusa},
	{path: paths_br.ajusa,component: Ajusa,meta : paths_br.seo.ajusa},

	//ACTUALIDAD
	{path: paths_es.actualidad,component: Actualidad,meta : paths_es.seo.actualidad},
	{path: paths_en.actualidad,component: Actualidad,meta : paths_en.seo.actualidad},
	{path: paths_ru.actualidad,component: Actualidad,meta : paths_ru.seo.actualidad},
	{path: paths_br.actualidad,component: Actualidad,meta : paths_br.seo.actualidad},

	//CONTACTO
	{path: paths_es.contacto,component: Contacto,meta : paths_es.seo.contacto},
	{path: paths_en.contacto,component: Contacto,meta : paths_en.seo.contacto},
	{path: paths_ru.contacto,component: Contacto,meta : paths_ru.seo.contacto},
	{path: paths_br.contacto,component: Contacto,meta : paths_br.seo.contacto},

	//BLOG
	{path: paths_es.blog,component: Blog,meta : paths_es.seo.blog},
	{path: paths_en.blog,component: Blog,meta : paths_en.seo.blog},
	{path: paths_ru.blog,component: Blog,meta : paths_ru.seo.blog},
	{path: paths_br.blog,component: Blog,meta : paths_br.seo.blog},

	//NOTICIA
	{path: `${paths_es.blog}/${paths_es.noticia}/:id`,component: Ficha_blog,meta : paths_es.seo.blog},
	{path: `${paths_en.blog}/${paths_en.noticia}/:id`,component: Ficha_blog,meta : paths_en.seo.blog},
	{path: `${paths_ru.blog}/${paths_ru.noticia}/:id`,component: Ficha_blog,meta : paths_ru.seo.blog},
	{path: `${paths_br.blog}/${paths_br.noticia}/:id`,component: Ficha_blog,meta : paths_br.seo.blog},

	//CATEGORIA
	{path: `${paths_es.blog}/${paths_es.categoria}/:id`,component: Categoria_blog,meta : paths_es.seo.blog},
	{path: `${paths_en.blog}/${paths_en.categoria}/:id`,component: Categoria_blog,meta : paths_en.seo.blog},
	{path: `${paths_ru.blog}/${paths_ru.categoria}/:id`,component: Categoria_blog,meta : paths_ru.seo.blog},
	{path: `${paths_br.blog}/${paths_br.categoria}/:id`,component: Categoria_blog,meta : paths_br.seo.blog},

	//SALA DE PRENSA
	{path: paths_es.sala_prensa,component: SalaPrensa,meta : paths_es.seo.sala_prensa},
	{path: paths_en.sala_prensa,component: SalaPrensa,meta : paths_en.seo.sala_prensa},
	{path: paths_ru.sala_prensa,component: SalaPrensa,meta : paths_ru.seo.sala_prensa},
	{path: paths_br.sala_prensa,component: SalaPrensa,meta : paths_br.seo.sala_prensa},

	//TRABAJA CON NOSOTROS
	{path: paths_es.trabaja_con_nosotros,component: TrabajaConNosotros,meta : paths_es.seo.trabaja_con_nosotros},
	{path: paths_en.trabaja_con_nosotros,component: TrabajaConNosotros,meta : paths_en.seo.trabaja_con_nosotros},
	{path: paths_ru.trabaja_con_nosotros,component: TrabajaConNosotros,meta : paths_ru.seo.trabaja_con_nosotros},
	{path: paths_br.trabaja_con_nosotros,component: TrabajaConNosotros,meta : paths_br.seo.trabaja_con_nosotros},

	//HISTORIA
	{path: paths_es.historia,component: Historia,meta : paths_es.seo.historia},
	{path: paths_en.historia,component: Historia,meta : paths_en.seo.historia},
	{path: paths_ru.historia,component: Historia,meta : paths_ru.seo.historia},
	{path: paths_br.historia,component: Historia,meta : paths_br.seo.historia},

	//MAPA WEB
	{path: paths_es.mapa_web,component: MapaWeb,meta : paths_es.seo.mapa_web},
	{path: paths_en.mapa_web,component: MapaWeb,meta : paths_en.seo.mapa_web},
	{path: paths_ru.mapa_web,component: MapaWeb,meta : paths_ru.seo.mapa_web},
	{path: paths_br.mapa_web,component: MapaWeb,meta : paths_br.seo.mapa_web},

	//POLITICA DE PRIVACIDAD
	{path: paths_es.politica,component: PoliticaPrivacidad,meta : paths_es.seo.politica},
	{path: paths_en.politica,component: PoliticaPrivacidadEN,meta : paths_en.seo.politica},
	{path: paths_ru.politica,component: PoliticaPrivacidadRU,meta : paths_ru.seo.politica},
	{path: paths_br.politica,component: PoliticaPrivacidadBR,meta : paths_br.seo.politica},

	//POLITICA DE GALLETAS
	{path: paths_es.politica_cookies,component: PoliticaCookies,meta : paths_es.seo.politica_cookies},
	{path: paths_en.politica_cookies,component: PoliticaCookiesEN,meta : paths_en.seo.politica_cookies},
	{path: paths_ru.politica_cookies,component: PoliticaCookiesRU,meta : paths_ru.seo.politica_cookies},
	{path: paths_br.politica_cookies,component: PoliticaCookiesBR,meta : paths_br.seo.politica_cookies},

	//AVISO LEGAL
	{path: paths_es.aviso_legal,component: AvisoLegal,meta : paths_es.seo.aviso_legal},
	{path: paths_en.aviso_legal,component: AvisoLegalEN,meta : paths_en.seo.aviso_legal},
	{path: paths_ru.aviso_legal,component: AvisoLegalRU,meta : paths_ru.seo.aviso_legal},
	{path: paths_br.aviso_legal,component: AvisoLegalBR,meta : paths_br.seo.aviso_legal},

	//DATOS SEGUROS
	{path: paths_es.datos_seguros,component: DatosSeguros,meta : paths_es.seo.datos_seguros},
	{path: paths_en.datos_seguros,component: DatosSegurosEN,meta : paths_en.seo.datos_seguros},
	{path: paths_ru.datos_seguros,component: DatosSegurosRU,meta : paths_ru.seo.datos_seguros},
	{path: paths_br.datos_seguros,component: DatosSegurosBR,meta : paths_br.seo.datos_seguros},

	{ path: "*", redirect: '/404' },
	{ path: "/404", component: Error404 }
	],
	scrollBehavior (to, from, savedPosition) {
		if(savedPosition) {return savedPosition}else{return {x:0,y:0}}
	},
});


import VueAnalytics from 'vue-analytics';

Vue.use(VueAnalytics, {
	id: 'UA-117270-3',
	router,
	debug : {sendHitTask: location.hostname.includes('ajusa.es')}
});

router.beforeEach((to, from, next) => {
	
	let meta;

	(to.path == "/") ? meta = to.meta[lang] : meta = to.meta;

	if(!!meta){	
		document.title = meta.title
		document.head.querySelector("[name=description]").content = meta.description;
	}

	next()
});

export default router;




