          <h3 class="title title-36">ПОЛИТИКА ЗАЩИТЫ ДАННЫХ</h3>
          <p>Руководство / Управляющий орган компании AUTO JUNTAS SAU (здесь и далее лицо, ответственное за обработку) берет на себя максимальную ответственность и обязательство по созданию, внедрению и обеспечению соблюдения этой Политики защиты данных, гарантируя постоянное повышение квалификации оператора данных с целью достижения совершенства в отношении соблюдения Регламента (ЕС) 2016/679 Европейского парламента и Совета от 27 апреля 2016 г. о защите физических лиц в отношении обработки персональных данных и свободного распространения этих данных и отмены Директивы 95/46/EC (Общие положения о защите данных) (OJEU L 119/1, 04-05-2016), а также испанского законодательства о защите персональных данных (Органический закон, конкретное отраслевое законодательство и его положения в области развития).</p>
          <br>
          <p>Политика защиты данных компании AUTO JUNTAS SAU основывается на принципе инициативной ответственности, согласно которой оператор отвечает за соблюдение нормативно-правовой базы, регулирующей указанную политику, и может продемонстрировать это компетентным контролирующим органам.</p>
          <br>
          <p>В этом контексте лицо, ответственное за обработку, будет руководствоваться следующими принципами, которые также должны будут служить в качестве ориентира и отправной точки при обработке персональных данных для всего персонала:</p>
          <br>
        
          <p>1. Защита данных с точки зрения проектирования: оператор будет применять, как в момент определения средств обработки, так и во время самой обработки, соответствующие технические и организационные меры, такие как псевдонимизация, предназначенные для эффективного использования принципов защиты данных, таких как минимизация данных, и интеграции необходимых гарантий в процедуру обработки.</p>
          <p>2. Защита данных по умолчанию: оператор данных будет применять соответствующие технические и организационные меры для гарантии обработки, по умолчанию, только тех персональных данных, которые будут необходимы для каждой конкретной цели такой обработки.</p>
          <p>3. Защита данных в жизненном цикле информации: меры, гарантирующие защиту персональных данных, будут применяться в течение всего жизненного цикла информации.</p>
          <p>4. Законность, лояльность и прозрачность: персональные данные будут обрабатываться законным, справедливым и прозрачным образом  в отношении заинтересованной стороны.</p>
          <p>5. Ограничение цели: персональные данные будут собираться для конкретных, четких и законных целей и не будут обрабатываться далее способом, несовместимым с указанными целями.</p>
          <p>6. Минимизация данных: персональные данные будут соответствующими, релевантными и ограниченными в рамках тех целей, для которых они обрабатываются.</p>
          <p>7. Точность: персональные данные будут точными и, при необходимости, будут обновляться; кроме того, будут предприняты все необходимые шаги для незамедлительного удаления или исправления неточных в отношении целей, для которых они обрабатывались, данных.</p>
          <p>8. Ограничение периода хранения: персональные данные будут храниться таким образом, чтобы позволить идентификацию заинтересованных сторон не дольше, чем того требуется для целей обработки персональных данных.</p>
          <p>9. Целостность и конфиденциальность: персональные данные будут обрабатываться таким образом, чтобы обеспечить соответствующую безопасность персональных данных, включая защиту от несанкционированного или незаконного обращения, а также от потери, уничтожения или случайного ущерба, путем применения соответствующих технических или организационных мер.</p>
          <p>10. Информация и обучение: одним из ключей, гарантирующих защиту персональных данных, является обучение и информация, предоставляемая персоналу, участвующему в их обработке. В течение жизненного цикла информации все сотрудники, имеющие доступ к данным, будут надлежащим образом обучены и проинформированы о своих обязательствах в отношении соблюдения правил защиты данных.</p>
          <br>
          <p>Политика защиты данных компании AUTO JUNTAS SAU доводится до ведома всех сотрудников оператора данных и предоставляется всем заинтересованным сторонам.</p>
          <br>
          <p>Как следствие, данная Политика защиты данных распространяется на весь персонал оператора данных, который должен знать и принимать ее, рассматривая ее как свою собственную, при этом каждый сотрудник несет ответственность за ее применение и проверку правил защиты данных, применимых к его деятельности, а также за выявление и внесение своего вклада в возможности улучшения, которое он считает уместным, для достижения совершенства в отношении ее соблюдения.</p>
          <br>
          <p>Данная Политика будет пересмотрена Дирекцией / Управляющим органом компании AUTO JUNTAS SAU столько раз, сколько это будет сочтено необходимым, с целью адаптации к действующим положениям о защите персональных данных.</p>
